import React, { useState, useEffect } from "react";
import "./TalkCard.scss";
import { Player } from "@lottiefiles/react-lottie-player";
import devAnimation from "../../assets/Animation - 1751634763450.json";
import FloatingSendButton from '../../components/FloatingSendButton/FloatingSendButton';
import successAnimation from "../../assets/email.json"; // Import your new success animation

export default function TalkCard({ talkDetails }) {
  const [messageText, setMessageText] = useState("");
  const [messageSent, setMessageSent] = useState(false);
  const [sending, setSending] = useState(false);
  const [showSuccessAnimation, setShowSuccessAnimation] = useState(false);

  useEffect(() => {
    if (messageSent) {
      setShowSuccessAnimation(true);
      const timer = setTimeout(() => {
        setShowSuccessAnimation(false);
        // Optional: If you want to allow sending another message immediately after the animation, uncomment this:
        // setMessageSent(false);
      }, 3000); // Display animation for 3 seconds
      return () => clearTimeout(timer);
    }
  }, [messageSent]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (messageText.trim().length === 0) return;
    setSending(true);
    setMessageSent(false); // Reset messageSent before new submission
    setShowSuccessAnimation(false); // Hide animation if retrying

    const formData = new FormData();
    formData.append("message", messageText);

    try {
      const res = await fetch("https://formspree.io/f/xjkrzqqr", {
        method: "POST",
        body: formData,
        headers: { Accept: "application/json" },
      });

      if (res.ok) {
        setMessageSent(true);
        setMessageText("");
      } else {
        alert("Something went wrong. Please try again.");
      }
    } catch {
      alert("Error sending message. Check your internet.");
    }

    setSending(false);
  };

  return (
    <div className="container">
      <div className="rectangle-wrapper">
        <div className="talkcard-content">
          {/* LEFT SIDE */}
          <div
            className={`rectangle talkcard-left ${talkDetails.isDark ? "dark-rectangle" : ""}`}
          >
            <div className="talk-card-title">{talkDetails.title}</div>
            <p className="talk-card-subtitle">{talkDetails.subtitle}</p>

            <div className="card-footer-button-div">
              {talkDetails.slides_url && (
                <a
                  href={talkDetails.slides_url}
                  className="talk-button"
                  target="_blank"
                  rel="noreferrer"
                >
                  Slides
                </a>
              )}
              {talkDetails.event_url && (
                <a
                  href={talkDetails.event_url}
                  className="talk-button"
                  target="_blank"
                  rel="noreferrer"
                >
                  Event
                </a>
              )}
            </div>
          </div>

          {/* RIGHT SIDE */}
          <div className="talkcard-right">
            <form onSubmit={handleSubmit} className="anonymous-form">
              <label htmlFor="message" className="anonymous-label">
                Drop an anonymous message:
              </label>

              <FloatingSendButton
                text={messageText}
                onChange={(e) => setMessageText(e.target.value)}
                onSubmit={handleSubmit}
              />

              {/* Only render the success animation, no text */}
              {showSuccessAnimation && (
                <div className="success-animation-container">
                  <Player
                    autoplay
                    loop={false}
                    src={successAnimation}
                    style={{ height: "100px", width: "100px" }}
                    speed={0.7}
                  />
                </div>
              )}
            </form>

            <div className="lf-player-container">
              <Player
                autoplay
                loop
                src={devAnimation}
                style={{ height: "300px", width: "300px" }}
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}