// src/components/FloatingSendButton/FloatingSendButton.jsx

import React, { useEffect, useRef, useState, useCallback } from 'react';
import { interpolate } from 'flubber';
import './FloatingSendButton.scss';

const FloatingSendButton = ({ text, onChange, onSubmit }) => {
  const textareaRef = useRef(null);
  const buttonRef = useRef(null);
  const pathRef = useRef(null);
  const animationFrameId = useRef(null);
  const morphData = useRef({ progress: 0, target: 0, start: null }).current;

  const [isFocused, setIsFocused] = useState(false);

  const getButtonPath = useCallback(() => {
    const button = buttonRef.current;
    if (!button) return null;

    const width = button.offsetWidth;
    const height = button.offsetHeight;
    const x = button.offsetLeft;
    const y = button.offsetTop;
    const r = 8; // Border radius

    return `M${x + r},${y} h${width - 2 * r} a${r},${r} 0 0 1 ${r},${r} v${height - 2 * r} a${r},${r} 0 0 1 -${r},${r} h-${width - 2 * r} a${r},${r} 0 0 1 -${r},-${r} v-${height - 2 * r} a${r},${r} 0 0 1 ${r},-${r} z`;
  }, []);

  useEffect(() => {
    const showButton = isFocused && text.trim().length > 0;
    const newTarget = showButton ? 1 : 0;

    if (morphData.target !== newTarget) {
        morphData.target = newTarget;
        morphData.start = null;
    }
  }, [isFocused, text, morphData]);

  useEffect(() => {
    const textarea = textareaRef.current;
    const path = pathRef.current;
    const button = buttonRef.current; // Get a reference to the button element

    const generateRopePath = (timestamp) => {
      if (!textarea) return '';
      const width = textarea.offsetWidth;
      const height = textarea.offsetHeight;
      const inset = 10, amplitude = 6, frequency = 0.025, speed = 0.001, step = 5;
      const time = timestamp * speed;
      let d = '', perimeterPos = 0;

      // (Rope generation logic is unchanged)
      const startY = inset + Math.sin(time) * amplitude;
      d += `M 0, ${startY}`;
      for (let x = step; x <= width; x += step) {
        perimeterPos = x;
        const y = inset + Math.sin(perimeterPos * frequency + time) * amplitude;
        d += ` L ${x},${y}`;
      }
      perimeterPos = width;
      const topRightY = inset + Math.sin(perimeterPos * frequency + time) * amplitude;
      d += ` L ${width},${topRightY}`;
      for (let y = step; y <= height; y += step) {
        perimeterPos = width + y;
        const x = width - inset + Math.sin(perimeterPos * frequency + time) * amplitude;
        d += ` L ${x},${y}`;
      }
      perimeterPos = width + height;
      const bottomRightX = width - inset + Math.sin(perimeterPos * frequency + time) * amplitude;
      d += ` L ${bottomRightX},${height}`;
      for (let x = width - step; x >= 0; x -= step) {
        perimeterPos = width + height + (width - x);
        const y = height - inset + Math.sin(perimeterPos * frequency + time) * amplitude;
        d += ` L ${x},${y}`;
      }
      perimeterPos = width + height + width;
      const bottomLeftY = height - inset + Math.sin(perimeterPos * frequency + time) * amplitude;
      d += ` L 0,${bottomLeftY}`;
      for (let y = height - step; y >= 0; y -= step) {
        perimeterPos = width + height + width + (height - y);
        const x = inset + Math.sin(perimeterPos * frequency + time) * amplitude;
        d += ` L ${x},${y}`;
      }
      d += ' Z';
      return d;
    }

    const animate = (timestamp) => {
      if (morphData.progress !== morphData.target) {
        if (morphData.start === null) morphData.start = timestamp;
        const elapsed = timestamp - morphData.start;
        const duration = 400;
        const t = Math.min(1, elapsed / duration);
        const easedT = t < 0.5 ? 2 * t * t : 1 - Math.pow(-2 * t + 2, 2) / 2;
        morphData.progress = morphData.target === 1 ? easedT : 1 - easedT;
      }

      const ropePath = generateRopePath(timestamp);
      const buttonPath = getButtonPath();

      if (ropePath && buttonPath && path && button) {
        const interpolator = interpolate(ropePath, buttonPath, { maxSegmentLength: 4 });
        const currentPath = interpolator(morphData.progress);
        path.setAttribute('d', currentPath);
        path.style.fillOpacity = morphData.progress;

        // --- THIS IS THE FIX ---
        // Sync the button's text opacity with the animation progress.
        button.style.opacity = 0.5 * morphData.progress;
      }

      animationFrameId.current = requestAnimationFrame(animate);
    }

    animationFrameId.current = requestAnimationFrame(animate);

    return () => {
      if (animationFrameId.current) {
        cancelAnimationFrame(animationFrameId.current);
      }
    };
  }, [getButtonPath, morphData]);

  return (
    <div className="textarea-container">
      <svg id="border-svg" xmlns="http://www.w3.org/2000/svg">
        <defs>
          <linearGradient id="rope-gradient" x1="0%" y1="0%" x2="100%" y2="100%">
            <stop offset="0%" stopColor="#38BDF8" />
            <stop offset="100%" stopColor="#A78BFA" />
          </linearGradient>
        </defs>
        <path ref={pathRef} id="wavy-border-path" stroke="url(#rope-gradient)" strokeWidth="3" strokeLinecap="round"/>
      </svg>

      <textarea
        ref={textareaRef}
        id="animated-textarea"
        name="message"
        placeholder="Write your message here..."
        value={text}
        onChange={onChange}
        onFocus={() => setIsFocused(true)}
        onBlur={() => setIsFocused(false)}
        required
      />

      <button
        ref={buttonRef}
        type="button"
        onClick={onSubmit}
        id="action-button"
      >
        Send
      </button>
    </div>
  );
};

export default FloatingSendButton;
